export interface WeatherData {
  location: string;
  description: string;
  temperature: string;
  condition: 'sunny' | 'cloudy' | 'rainy' | 'snowy' | 'clear' | 'night';
  icon: string;
  humidity?: string;
  windSpeed?: string;
  moonPhase?: string; // 
}

export interface DiaryEntry {
  id: string;
  title: string;
  content: string;
  mood: {
    emoji: string;
    type: string;
  };
  weather: WeatherData;
  timestamp: number;
}

export interface MoodOption {
  emoji: string;
  type: string;
  label: string;
}

export const MOOD_OPTIONS: MoodOption[] = [
  { emoji: '', type: 'happy', label: '' },
  { emoji: '', type: 'sad', label: '' },
  { emoji: '', type: 'excited', label: '' },
  { emoji: '', type: 'calm', label: '' },
  { emoji: '', type: 'angry', label: '' },
  { emoji: '', type: 'tired', label: '' },
];

