import { MoonPhase } from '../types';

// 
export const calculateMoonPhase = (date: Date): MoonPhase => {
  // 
  //  Julian Day Number 
  
  const year = date.getFullYear();
  const month = date.getMonth() + 1;
  const day = date.getDate();
  
  //  Julian Day Number
  let jd = 367 * year - Math.floor(7 * (year + Math.floor((month + 9) / 12)) / 4) + Math.floor(275 * month / 9) + day + 1721013.5;
  
  // 200016
  const newMoon2000 = 2451550.1;
  const daysSinceNewMoon2000 = jd - newMoon2000;
  
  // 29.53058867
  const lunarCycle = 29.53058867;
  const phase = (daysSinceNewMoon2000 % lunarCycle) / lunarCycle;
  
  // 
  if (phase < 0.0625) return 'new';           //  (0-1/16)
  if (phase < 0.1875) return 'waxing_crescent'; //  (1/16-3/16)
  if (phase < 0.3125) return 'first_quarter';   //  (3/16-5/16)
  if (phase < 0.4375) return 'waxing_gibbous';  //  (5/16-7/16)
  if (phase < 0.5625) return 'full';           //  (7/16-9/16)
  if (phase < 0.6875) return 'waning_gibbous';  //  (9/16-11/16)
  if (phase < 0.8125) return 'last_quarter';    //  (11/16-13/16)
  if (phase < 0.9375) return 'waning_crescent'; //  (13/16-15/16)
  return 'new'; //  (15/16-1)
};

// 
export const getMoonPhaseName = (phase: MoonPhase): string => {
  const phaseNames = {
    new: '',
    waxing_crescent: '',
    first_quarter: '',
    waxing_gibbous: '',
    full: '',
    waning_gibbous: '',
    last_quarter: '',
    waning_crescent: ''
  };
  return phaseNames[phase];
};

// 
export const getMoonPhaseDescription = (phase: MoonPhase): string => {
  const descriptions = {
    new: '',
    waxing_crescent: '',
    first_quarter: '',
    waxing_gibbous: '',
    full: '',
    waning_gibbous: '',
    last_quarter: '',
    waning_crescent: ''
  };
  return descriptions[phase];
}; 